import React, { useCallback } from 'react';
import Particles from 'react-tsparticles';
import { loadFull } from 'tsparticles';
import styled from 'styled-components';

const ParticlesWrapper = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1; /* Place behind all other content */
`;

const ParticleSkills = () => {
  const particlesInit = useCallback(async (engine) => {
    await loadFull(engine);
  }, []);

  const particlesOptions = {
    particles: {
      number: {
        value: 40, // Adjust the number of particles (floating items)
        density: {
          enable: true,
          value_area: 800,
        },
      },
      shape: {
        type: 'circle', // Shape type (we'll use circles instead of icons directly)
      },
      color: {
        value: ['#007396', '#4B8BBE', '#61DBFB', '#E34C26', '#1572B6', '#339933', '#F05032', '#181717', '#A8B9CC', '#4479A1', '#003B57', '#092E20', '#F7DF1E'], // Skill icon colors
      },
      opacity: {
        value: 0.8,
      },
      size: {
        value: 30, // Size of the particles (adjust this to your preference)
        random: true,
      },
      move: {
        enable: true,
        speed: 2,
        direction: 'none',
        random: true,
        straight: false,
        out_mode: 'out',
        bounce: false,
      },
    },
    interactivity: {
      detect_on: 'canvas',
      events: {
        onhover: {
          enable: true,
          mode: 'repulse',
        },
      },
      modes: {
        repulse: {
          distance: 100,
        },
      },
    },
    retina_detect: true,
  };

  return (
    <ParticlesWrapper>
      <Particles id="tsparticles" init={particlesInit} options={particlesOptions} />
    </ParticlesWrapper>
  );
};

export default ParticleSkills;
